import * as THREE from 'three'
import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls.js';
// import { OBJLoader } from 'three/examples/jsm/loaders/OBJLoader';
import { OBJLoader } from 'three-obj-mtl-loader'
function three_init({width,height,id="threeId",init}={}){
	/**初始化3d
	   @@参数{a=1}={}这种写法调用的时候不用传值代表默认传空对象，可以直接使用设置的默认参数
	   @@参数{a=1}这种写法调用的时候必须传参数对象，传空值才能使用默认值，可以直接使用设置的默认参数
	**/
	var _group;
	// 模型加载
	var loader = new OBJLoader();
	var textureLoader = new THREE.TextureLoader();


	var scene = new THREE.Scene();
	var axisHelper = new THREE.AxisHelper(250);
	scene.add(axisHelper);

	/**
	 * 光源设置
	 */
	//点光源
	var point = new THREE.PointLight(0xffffff);
	point.position.set(400, 200, 300); //点光源位置
	scene.add(point); //点光源添加到场景中
	//环境光
	var ambient = new THREE.AmbientLight(0x444444);
	scene.add(ambient);
	// console.log(scene)
	// console.log(scene.children)
	/**
	 * 相机设置
	 */
	var _width = width || window.innerWidth; //窗口宽度
	var _height =height ||  window.innerHeight; //窗口高度
	var k = _width / _height; //窗口宽高比
	var s = 50; //三维场景显示范围控制系数，系数越大，显示的范围越大
	//创建相机对象
	var camera = new THREE.OrthographicCamera(-s * k, s * k, s, -s, 1, 1000);
	camera.position.set(300, 300, 100); //设置相机位置
	camera.lookAt(scene.position); //设置相机方向(指向的场景对象)
	/**
	 * 创建渲染器对象
	 */
	var renderer = new THREE.WebGLRenderer();
	renderer.setSize(_width, _height);//设置渲染区域尺寸
	renderer.setClearColor(0xb9d3ff, 1); //设置背景颜色
	document.getElementById(id).appendChild(renderer.domElement);//在div中插入canvas对象
	// document.body.appendChild(renderer.domElement); //body元素中插入canvas对象
	//执行渲染操作   指定场景、相机作为参数
	function render() {
		requestAnimationFrame(render);
		// console.log(_group)
		// _group.rotation.x += 0.01;
		_group.rotation.y += 0.01;
	    renderer.render(scene,camera);//执行渲染操作
	}
	// render()

	var controls = new OrbitControls(camera,renderer.domElement);//创建控件对象
	// controls.addEventListener('change', render);//监听鼠标、键盘事件


	const modLod = new Promise((resolve, reject) => {
	   loader.load('static/yzt.obj',function (obj) {
	      resolve(obj)
	    })
	})

	const truLod = new Promise((resolve, reject) => {
	    textureLoader.load('static/yuanzhuti.jpg', function(texture) {
	        resolve(texture)
	    })
	})

	Promise.all([modLod, truLod]).then(data=>{
	    const obj = data[0]
	    const texture = data[1]
	    // console.log(texture)
	    obj.children[0].material.map = texture;
	    obj.children[0].material.needsUpdate=true;
	    scene.add(obj);
	    _group = obj
	    render()
	    if(init)init({scene:scene})
	})
}
export  {
  three_init
}